export default "import { Component } from '@angular/core';\nimport { AlertComponent } from 'bao-tran-angular/alert/alert.component';\n\n@Component({\n  selector: 'demo-alert-timeout',\n  templateUrl: './dismiss-on-timeout.html'\n})\nexport class DemoAlertTimeoutComponent {\n  alerts: any[] = [{\n    type: 'success',\n    msg: `Well done! You successfully read this important alert message. (added: ${new Date().toLocaleTimeString()})`,\n    timeout: 5000\n  }];\n\n  add(): void {\n    this.alerts.push({\n      type: 'info',\n      msg: `This alert will be closed in 5 seconds (added: ${new Date().toLocaleTimeString()})`,\n      timeout: 5000\n    });\n  }\n\n  onClosed(dismissedAlert: AlertComponent): void {\n    this.alerts = this.alerts.filter(alert => alert !== dismissedAlert);\n  }\n}\n";